.friendMessage {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 30px;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
    text-align: left;
}

.friendImageAndText img {
    width: 48px;
}

.friendText {
    background-color: #F5F7FB;
    padding: 7px 13px 8px 13px;
    color: #fff;
    border-radius: 12px 12px 12px 0px;
    position: relative;
    margin-left: 12px;
    box-shadow: 0px 1px 2px 0px #1D21261A;
}

.friendText:after {
    content: " ";
    position: absolute;
    left: -12px;
    bottom: 0px;
    border-top: 12px solid transparent;
    border-right: 12px solid #F5F7FB;
    border-left: none;
    border-bottom: none;
    box-shadow: 0px 1px 2px 0px #1D21261A;
}

.friendName {
    font-weight: 600;
    font-size: 18px;
    line-height: 21.94px;
    font-family: "Montserrat", sans-serif;
    color: #000;
}

.friendMessageText {
    font-size: 16px;
    line-height: 19.5px;
    font-weight: 400;
    margin-top: 4px;
    font-family: "Montserrat", sans-serif;
    color: #000;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    color: #000;
    width: 26px;
    text-align: center;
    margin: 5px 0px 0px 11px;
}
