.message {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    text-align: right;
}

.imageAndText img {
    width: 48px;
}

.text {
    background-color: #0066CC;
    padding: 7px 13px 8px 13px;
    color: #fff;
    border-radius: 12px 12px 0px 12px;
    position: relative;
    margin-right: 12px;
}

.text:after {
    content: " ";
    position: absolute;
    right: -12px;
    bottom: 0px;
    border-radius: 0px 4px 0px 0px;
    border-top: 12px solid transparent;
    border-right: none;
    border-left: 12px solid #0066CC;
    border-bottom: none;
}

.name {
    font-weight: 600;
    font-size: 18px;
    line-height: 21.94px;
    font-family: "Montserrat", sans-serif;
}

.messageText {
    font-size: 16px;
    line-height: 19.5px;
    font-weight: 400;
    margin-top: 4px;
    font-family: "Montserrat", sans-serif;
}

.time {
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    color: #000;
    width: 26px;
    text-align: center;
    margin: 5px 11px 0px 0px;
}