.sendForm {
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 40px;
    margin-top: 56px;
}

.textarea {
    border-radius: 20px;
    width: 80%;
    overflow: hidden;
    background-color: #F5F7FB;
    resize: none;
    color: rgba(0, 0, 0, .6);
    font-weight: 400;
    font-size: 14px;
    line-height: 17.07px;
    padding: 12px 0px 11px 24px;
    border: none;
    box-shadow: 0px 1px 2px 0px #1D21261A;
}

.button {
    height: 100%;
    width: 120px;
    border-radius: 20px;
    background-color: #0066CC;
    border: none;
    color: #fff;
    font-size: 16px;
    line-height: 19.5px;
    font-weight: 600;
    cursor: pointer;
}

.button:hover {}

.button:active {}